.SignUp{
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    &__Auth-form {
        width: 420px;
        box-shadow: rgb(0 0 0 / 16%) 1px 1px 10px;
        padding-top: 30px;
        padding-bottom: 20px;
        border-radius: 8px;
        background-color: white;
    }
    &__SubContainer{
        padding-left: 12%;
        padding-right: 12%;
    }
    &__Auth-form-title {
        text-align: center;
        margin-bottom: 1em;
        font-size: 24px;
        color: rgb(34, 34, 34);
        font-weight: 800;
    }
}

label {
    font-size: 14px;
    font-weight: 600;
    color: rgb(34, 34, 34);
}